import React, { Component } from 'react' 
import {Modal, Button} from 'react-bootstrap'
import { HUBSPOT_URL } from '../../../../../actions/api'

export default class ContactModal extends Component { 
 
  render() {
    const { show, onCloseModel} = this.props 

    return (
        <Modal size="lg" show={show} centered>
            <Modal.Body className="p-lg-7 p-3 demo_schedule position-relative">
              <Button variant='close' className="m-5 position-absolute end-0 top-0" style={{zIndex:'99'}} aria-label="Close" onClick={onCloseModel}></Button>
              <iframe src={HUBSPOT_URL} width='100%' height={700}></iframe>
            </Modal.Body> 
        </Modal>
    )
  }
}

