import React, { Component } from 'react'
import { PLAN__DETAILS, UPDATE_PLAN__DETAILS } from '../../../../actions/planDetail';
import { TOAST_DISMISS, TOAST_LOADING, getCookieValue } from '../../../../utils';
import withRouter from '../../../routes/WithRouter';
import Cookies from 'universal-cookie';
import { connect } from 'react-redux';
import PlanView from './Elements/PlanView';
import { emitEvent, onEvent } from '../../../../actions/socketClient';
import { HOST_URL } from '../../../../actions/api';
import { createWall } from '../../../../actions/initActions';
import { SHOPIFY_PRODUCT_SYNC } from '../../../../actions/productAction';

class SignUpPricePlan extends Component {
  state = { init: false, showFrePlan: false, shopifyLogin: localStorage.getItem('shopifyLogin'), loading: false, changePrice: true, showContactUs: false }

  componentDidMount() {
    this.props.PLAN__DETAILS();
    this.setState({ shopifyLogin: localStorage.getItem('shopifyLogin') })
    setTimeout(() => {
      onEvent(`getPlanUpdated${this.props.authorized?.token?.user?.id != null ? this.props.authorized?.token?.user?.id : this.props.authorized?.userData?.user_data?.user.id ? this.props.authorized?.userData?.user_data?.user.id : localStorage.getItem('ownerId')}`, (data) => {
        localStorage.removeItem("steps")
        window.location.href = `${HOST_URL}walls/home`
      });
    }, 1500);
    window.fpr("referral", { email: localStorage.getItem('emailId') })
  }



  SignUpPlanUpdate = async (planId) => {
    const { authorized } = this.props;
    this.setState({ loading: true });
    const toastId = TOAST_LOADING();
    const cookies = new Cookies();
    try {
      const response = await UPDATE_PLAN__DETAILS(planId);
      if (response.data.response_code !== 200) throw new Error("Plan update failed");

      const userId = authorized?.token?.user?.id || authorized?.userData?.user_data?.user.id || localStorage.getItem('ownerId');
      const createFromShopify = authorized?.userData?.user_data?.sessionData?.createFrom === 'shopify';

      const processResponse = () => {
        //await createWall(shopOnBio);
        this.setState({ loading: false, init: true });
        localStorage.removeItem('steps');
        cookies.remove('shoponUrl');
        setTimeout(() => {
          window.location.replace(`${HOST_URL}walls/home?id=tagshop`);
        }, 100);
      };

      if (createFromShopify) {
        //await createWall(null);
        localStorage.removeItem('shopifyLogin');
        localStorage.removeItem('steps');
        setTimeout(() => {
          SHOPIFY_PRODUCT_SYNC(localStorage.getItem('shopId'), () => {
            window.location.href = `${HOST_URL}init/index/shopify`;
          });
        }, 100);
      } else {
        emitEvent('planUpdated', { id: userId, plan: "" });
        const shopOnBio = getCookieValue('shoponUrl');
        await processResponse(shopOnBio);
      }
      TOAST_DISMISS(toastId);
    } catch (error) {
      this.setState({ loading: false });
      TOAST_DISMISS(toastId);
    }
  };


  render() {
    return <PlanView {...this.props} signUpPlan={true} loading={this.state.loading} SignUpPlanUpdate={this.SignUpPlanUpdate} />
  }
}

const mapStateToProps = state => {
  return {
    authorized: state.authorized,
    priceDetail: state.planPriceDetail?.planPrice,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    PLAN__DETAILS: () => dispatch(PLAN__DETAILS()),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SignUpPricePlan));